import { createContext, ReactNode, useContext, useMemo, useState } from "react";

import {
  BusinessSettingsApiInterface as BSAType,
  BusinessSettingsDataInterface as BSDType,
  LeadDistributionRulesInterface as LDRType,
  InvitedMemberInterface,
  LeadDataFieldInterface,
  TeamMemberInterface,
  WATemplateDataInterface,
  WATemplateCategory,
  WATemplateLanguage,
  WATemplateType,
  WATemplateHeaderType,
  WATemplateButtonType,
  WATemplateButtonCtaType,
  IAccounts,
  ITWUser,
  ILIUser,
  ISmPermission,
} from "Shared/Interfaces/Business/BusinessSettings";
import { BusinessTimeZoneInterface } from "Shared/Interfaces/Business/BusinessProfile";
import allowedVerticalsData from "Constants/Data/allowed-verticals.json";
import countriesData from "Constants/Data/countries.json";
import timeZonesData from "Constants/Data/time-zones.json";
import industriesData from "Constants/Data/industries.json";
import waTemplateCategoryData from "Constants/Data/wa-template-categories.json";
import waTemplateLanguagesData from "Constants/Data/languages.json";

const SettingsData = createContext<BSDType | undefined>(undefined);
const SettingsApi = createContext<BSAType | undefined>(undefined);

export const SettingsProvider = ({ children }: { children: ReactNode }) => {
  const countriesList: any[] = countriesData;
  const [provinces, setProvinces] = useState<any[]>([]);
  const [showInviteForm, setShowInviteForm] = useState<boolean>(false);
  const [invitedMembers, setInvitedMembers] = useState<
    InvitedMemberInterface[]
  >([]);
  const [teamMembers, setTeamMembers] = useState<TeamMemberInterface[]>([]);
  const [teamMembersTotal, setTeamMembersTotal] = useState<
    number | undefined
  >();
  const [invitedMemberFilter, setInvitedMemberFilter] = useState<string>("");
  const [leadDataFields, setLeadDataFields] = useState<
    LeadDataFieldInterface[]
  >([]);
  const [leadDistributionRules, setLeadDistributionRules] = useState<LDRType[]>(
    []
  );
  const [showLdfForm, setShowLdfForm] = useState<boolean>(false);
  const [showLdrForm, setShowLdrForm] = useState<boolean>(false);
  const [editLdfId, setEditLdfId] = useState<number | undefined>();
  const [totalLdRules, setTotalLdRules] = useState<number>(0);
  const [ldfFilter, setLdfFilter] = useState<string>("");
  const [watFilter, setWatFilter] = useState<string>("");
  const [totalWaTemplates, setTotalWaTemplates] = useState<number>(0);
  const waTemplateCategories: WATemplateCategory[] = waTemplateCategoryData;
  const waTemplateLanguages: WATemplateLanguage[] = waTemplateLanguagesData;
  const waTemplateTypes: WATemplateType[] = [
    {
      value: "text",
      label: "Text only",
    },
    {
      value: "media",
      label: "Media & Interactive",
    },
  ];
  const waTemplateHeaderTypes: WATemplateHeaderType[] = [
    {
      name: "Text",
      value: "TEXT",
    },
    {
      name: "Image",
      value: "IMAGE",
    },
    {
      name: "Video",
      value: "VIDEO",
    },
    {
      name: "Documents",
      value: "DOCUMENT",
    },
  ];
  const waTemplateButtonTypes: WATemplateButtonType[] = [
    {
      name: "CALL_TO_ACTION",
      label: "Call to Action",
    },
    {
      name: "QUICK_REPLY",
      label: "Quick Reply",
    },
  ];
  const waTemplateButtonCtaTypes: WATemplateButtonCtaType[] = [
    {
      name: "URL",
      value: "URL",
    },
    {
      name: "Phone Number",
      value: "PHONE_NUMBER",
    },
  ];
  const [waTemplatesData, setWaTemplatesData] = useState<
    WATemplateDataInterface[]
  >([]);
  const [editWaTemplateId, setEditWaTemplateId] = useState<
    number | undefined
  >();
  const [previewWaTemplateId, setPreviewWaTemplateId] = useState<
    number | undefined
  >();
  const [showWaTemplateForm, setShowWaTemplateForm] = useState<boolean>(false);
  const [previewWaTemplateData, setPreviewWaTemplateData] = useState<
    any | null
  >(null);
  const [showWaPreviewForm, setShowWaPreviewForm] = useState<boolean>(false);
  const [showWaTemplatePreview, setShowWaTemplatePreview] =
    useState<boolean>(false);
  const [connectedAccounts, setConnectedAccounts] = useState<IAccounts[]>([]);
  const [unconnectedAccounts, setUnconnectedAccounts] = useState<string[]>([]);
  const [TWUser, setTWUser] = useState<ITWUser | null>(null);
  const [LIUser, setLIUser] = useState<ILIUser | null>(null);
  const [platform, setPlatform] = useState<string | null>(null);
  const [permissions, setPermissions] = useState<ISmPermission>({
    show: false,
    provider: "",
  });
  const industryList: string[] = industriesData;
  const allowedVerticals: { label: string; value: string }[] =
    allowedVerticalsData;
  const timeZoneList: BusinessTimeZoneInterface[] = timeZonesData;
  const api = useMemo<BSAType>(
    () => ({
      setProvinces,
      setInvitedMembers,
      setTeamMembers,
      setTeamMembersTotal,
      setShowInviteForm,
      setInvitedMemberFilter,
      setLeadDataFields,
      setLdfFilter,
      setShowLdfForm,
      setEditLdfId,
      setLeadDistributionRules,
      setShowLdrForm,
      setTotalLdRules,
      setWatFilter,
      setTotalWaTemplates,
      setWaTemplatesData,
      setEditWaTemplateId,
      setPreviewWaTemplateId,
      setShowWaTemplateForm,
      setPreviewWaTemplateData,
      setShowWaPreviewForm,
      setShowWaTemplatePreview,
      setConnectedAccounts,
      setUnconnectedAccounts,
      setTWUser,
      setLIUser,
      setPlatform,
      setPermissions,
    }),
    []
  );

  return (
    <SettingsApi.Provider value={api}>
      <SettingsData.Provider
        value={{
          allowedVerticals,
          timeZoneList,
          industryList,
          countriesList,
          provinces,
          invitedMembers,
          teamMembers,
          teamMembersTotal,
          invitedMemberFilter,
          showInviteForm,
          leadDataFields,
          ldfFilter,
          showLdfForm,
          editLdfId,
          leadDistributionRules,
          showLdrForm,
          totalLdRules,
          watFilter,
          totalWaTemplates,
          waTemplateCategories,
          waTemplateLanguages,
          waTemplateTypes,
          waTemplateHeaderTypes,
          waTemplateButtonTypes,
          waTemplatesData,
          editWaTemplateId,
          previewWaTemplateId,
          showWaTemplateForm,
          previewWaTemplateData,
          showWaPreviewForm,
          showWaTemplatePreview,
          waTemplateButtonCtaTypes,
          connectedAccounts,
          unconnectedAccounts,
          TWUser,
          LIUser,
          platform,
          permissions,
        }}
      >
        {children}
      </SettingsData.Provider>
    </SettingsApi.Provider>
  );
};

export const useSettingsData = () => {
  const context = useContext(SettingsData);
  if (context === undefined) {
    throw Error(
      "Item must be used inside of SettingsProvider, otherwise it will not function correctly."
    );
  }
  return context;
};

export const useSettingsApi = () => {
  const context = useContext(SettingsApi);
  if (context === undefined) {
    throw Error(
      "Item must be used inside of SettingsProvider, otherwise it will not function correctly."
    );
  }
  return context;
};
